@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@900&family=Roboto:wght@100;300;400;500;700;900&display=swap');



.editable {
    width: 100%;
  }
  
  .editable_display {
    padding: 6px 12px;
    border-radius: 3px;
    background-color: rgb(105, 192, 255);
    color: #000;
    cursor: pointer;
    width: fit-content;
    transition: 200ms;
  }
  
  .editable_display:hover {
    background-color: rgb(238, 242, 245);

  }
  
  .editable_edit {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .editable_edit input {
    border: 2px solid #0079bf;
    border-radius: 5px;
    outline: none;
    font-size: 1rem;
    padding: 10px;
  }
  
  .editable_edit_footer {
    display: flex;
    gap: 8px;
    align-items: center;
  }
  
  .editable_edit_footer button {
    cursor: pointer;
    border-radius: 5px;
    outline: none;
    background-color: #0079bf;
    color: #fff;
    border: none;
    transition: 100ms ease;
    padding: 10px;
  }
  .editable_edit_footer button:hover {
    background-color: #046daa;
  }
  
  .editable_edit_footer button:active {
    transform: translateY(2px);
  }
  
  .editable_edit_footer .closeIcon {
    cursor: pointer;
    height: 24px;
    width: 24px;
  }